<style lang="scss">@import "./css/style.scss";</style>
<template>
    <div id="selectCoursePack">
        <ToolBar>
            <div></div>
            <div>
                <SelectTags v-model="labelList"></SelectTags>
                <el-input
                        placeholder="请输入课程名称或课程ID"
                        size="small"
                        style="width: 220px"
                        v-model="searchParams.postTitle"
                        clearable>
                </el-input>
                <el-select v-model="searchParams.postType" size="small" clearable placeholder="操作人"
                           style="width: 140px">
                    <el-option
                            v-for="(v,k) in $Cfg.postType"
                            :key="k"
                            :label="v"
                            :value="k">
                    </el-option>
                </el-select>

                <el-button type="success" size="small" @click="refresh = !refresh">查询</el-button>
                <el-button type="primary" size="small">添加</el-button>
            </div>
        </ToolBar>
        <el-table
                :data="tableData"
                border
                style="width: 100%">
            <el-table-column
                type="selection"
                width="55">
            </el-table-column>
            <el-table-column
                    prop="date"
                    label="ID"
                    width="180">
            </el-table-column>
            <el-table-column
                    prop="name"
                    label="课程包名称"
                    width="180">
            </el-table-column>
            <el-table-column
                    prop="address"
                    label="运营标签">
            </el-table-column>
            <el-table-column
                    prop="address"
                    label="原子课数量">
            </el-table-column>
            <el-table-column
                prop="address"
                label="课程包数量">
            </el-table-column>
            <el-table-column
                prop="address"
                label="试卷数量">
            </el-table-column>
            <el-table-column
                prop="address"
                label="课程状态">
            </el-table-column>
            <el-table-column
                prop="address"
                label="最近操作人">
            </el-table-column>
            <el-table-column
                prop="address"
                label="创建时间">
            </el-table-column>
            <el-table-column
                    label="操作"
                    width="78">
                <template slot-scope="scope">
                    <el-button @click="handleClick(scope.row)" type="info" size="small"
                               >预览</el-button>
                </template>
            </el-table-column>
        </el-table>
        <Pagination
            :params="searchParams"
            :requestFunc="requestFunc"
            @returnData="returnData"
        />
    </div>
</template>

<script>
    import ToolBar from '@/components/ToolBar.vue';
    import HelpHint from '@/components/HelpHint.vue';
    import Pagination from '@/components/Pagination.vue';
    import paginationMixin from './mixin/page.js'
    import SelectTags from '@/components/SelectTags/SelectTags.vue';

    export default {
        mixins:[paginationMixin],
        data() {
            return {
                labelList: [],
                tableData: [{
                    date: '2016-05-03',
                    name: 'Lorem ipsum dolor sit amet,',
                    address: 'Lorem ipsum dolor sit amet, consectetur adipisicing elit. Asperiores fugit in quae vero. Adipisci blanditiis dignissimos eum facere laudantium quasi ratione repellat vitae! Alias consequatur dolores enim neque similique unde.'
                }, {
                    date: '2016-05-02',
                    name: 'Lorem ipsum dolor sit amet,',
                    address: 'Lorem ipsum dolor sit amet, consectetur adipisicing elit. Asperiores fugit in quae vero. Adipisci blanditiis dignissimos eum facere laudantium quasi ratione repellat vitae! Alias consequatur dolores enim neque similique unde.'
                }]
            }
        },
        methods: {
            handleClick(row) {
                this.$alert(row, '标题名称', {
                    confirmButtonText: '确定',
                    callback: action => {}
                });
            },

        },
        created() {

        },
        components: {
            ToolBar, HelpHint, Pagination, SelectTags
        }
    }
</script>
